/* Brand highlight cards */
.brand-highlight {
  border-left-width: 4px;
  transition: all 0.3s ease;
}

.brand-highlight:hover {
  transform: translateY(-2px);
  box-shadow: var(--glow-accent);
}

.brand-moser {
  border-left-color: var(--brand-moser);
}

.brand-jlc {
